* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
body, html {
  height: 100%;
  font-size: 100%;
  overflow: hidden;
}



.backgroundLogin {
  list-style-type: none;
}

/** backgroundLogin **/
.backgroundLogin,
.backgroundLogin:after { 
    top: 0px; /*Not sure why I needed this fix*/
    position: fixed;
    width: 100%;
    height: 100%;
    left: 0px;
    z-index: 0; 
}

.backgroundLogin li span { 
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0px;
    left: 0px;
    color: transparent;
    background-size: cover;
    background-position: 50% 50%;
    background-repeat: no-repeat;
    opacity: 0;
    z-index: 0;
    animation: imageAnimation 30s linear infinite 0s; 
}



.backgroundLogin li:nth-child(1) span { 
    background-image: url("./loginbg1.jpg"); 
}
.backgroundLogin li:nth-child(2) span { 
    background-image: url("./loginbg2.jpg");
    animation-delay: 8s; 
}
.backgroundLogin li:nth-child(3) span { 
    background-image: url("./loginbg3.jpg");
    animation-delay: 16s; 
}
.backgroundLogin li:nth-child(4) span { 
    background-image: url("./loginbg4.jpg");   
    animation-delay: 24s; 
}
.backgroundLogin li:nth-child(5) span { 
    background-image: url("./loginbg5.jpg");
    animation-delay: 32s; 
}



@keyframes imageAnimation { 
    0% { opacity: 0; animation-timing-function: ease-in; }
    8% { opacity: 1; animation-timing-function: ease-out; }
    17% { opacity: 1 }
    25% { opacity: 0 }
    100% { opacity: 0 }
}


@keyframes titleAnimation { 
    0% { opacity: 0 }
    8% { opacity: 1 }
    17% { opacity: 1 }
    19% { opacity: 0 }
    100% { opacity: 0 }
}


.no-cssanimations .cb-backgroundLogin li span {
  opacity: 1;
}

.LoginBox {
  position: absolute;
  max-width: 300px;
  width: 100%;
  background: #fff;
  border-radius: 4px;
  padding: 1.187rem 1.875rem 2.062rem 1.875rem;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}
.LoginBox .team7-logologin {
  background: url("./houseforeld.png");
  background-size: 230px 60px;
  position: relative;
  text-align: center;
  background-repeat: no-repeat;
  filter: invert(100%);
  display: block;
  width: 230px;
  height: 60px;
  max-width: 100%;
  max-height: 100%;
  margin: 0.875rem auto 0;
}
.LoginBox .UserLogin {
  font-size: 0.875rem;
  font-weight: bold;
  color: #575674;
  line-height: 1.062rem;
}
.LoginBox .UserLogin .control-Element {
  display: block;
  margin-top: 0.875rem;
  margin-bottom: 0.437rem;
}
.LoginBox .UserLogin .login-control {
  background: #eeeef1;
  border-radius: 4px;
  border: 0 none;
  padding: 0.75rem 0.625rem;
  width: 100%;
  color: #575674;
  font-size: 0.875rem;
}
.LoginBox .UserLogin .login-control:focus {
  outline: none;
  border: 0 none;
}
.LoginBox .submitLogin {
  display: block;
  margin-top: 1.475rem;
  width: 100%;
  text-align: center;
  font-size: 1rem;
  line-height: 1.25rem;
  border-radius: 4px;
  border: 0 none;
  background: #000000;
  color: #fff;
  padding: 10px 0;
}
.LoginBox .submitLogin:focus {
  outline: none;
  border: 0 none;
}
.LoginBox .submitLogin:hover {
  background-color: rgb(95, 94, 94);
  color: white;
}
.LoginBox .Login-forgetRegister {
  align-items: center;
  justify-content: space-between;
  display: block;
  margin-top: 12px;
  font-size: 0.75rem;
  font-weight: normal;
  line-height: 17px;
  color: gray;
  padding-left: 4px;
}
.LoginBox .Login-forgetRegister .registerUser, .LoginBox .Login-forgetRegister .forgetPassword{
  right: 0px;
  left: 200vw;
  color: gray;
}
.LoginBox .Login-forgetRegister .registerUser:hover, .LoginBox .Login-forgetRegister .forgetPassword:hover {
  color:black;
}
.team7Copyright {
  position: absolute;
  left: 0;
  right: 0;
  bottom: 0;
  width: 100%;
  padding-bottom: 2px;
}
.team7CopyLeft {
  float: left;
}
.team7CopyLeft a {
  text-decoration: none;
  color: #fff;
  font-size: 0.75rem;
  padding-right: 10px;
  float: left;
  line-height: 18px;
  text-transform: uppercase;
}
.team7CopyRight {
  float: right;
  color: #fff;
  font-size: 0.75rem;
  line-height: 18px;
}
.team7CopyRight a {
  text-decoration: none;
  color: #f83;
}
.clear {
  clear: both;
}
@media only screen and (max-width: 800px) {
  html {
    font-size: 80%;
 }
}
@media only screen and (max-width: 480px) {
  .team7CopyLeft {
    float: none;
    text-align: center;
 }
  .team7CopyLeft a {
    float: none;
 }
  .team7CopyRight {
    float: none;
    text-align: center;
    margin-top: 5px;
 }
}
